import React from "react";
import HomeIcon from "@mui/icons-material/Home";
import TimelineIcon from "@mui/icons-material/Timeline";
import AttachMoneyIcon from "@mui/icons-material/AttachMoney";
import PersonIcon from "@mui/icons-material/Person";
import CardGiftcardIcon from "@mui/icons-material/CardGiftcard";
import AccountBalanceIcon from "@mui/icons-material/AccountBalance";
import AssessmentIcon from "@mui/icons-material/Assessment";
import EmailIcon from "@mui/icons-material/Email";
import FeedbackIcon from "@mui/icons-material/Feedback";
import MessageIcon from "@mui/icons-material/Message";
import "./Sidebar.css";
import { Link } from "react-router-dom";

export const Sidebar = () => {
  return (
    <div className="sidebar">
      <div className="sidebarWrapper">
        <div className="sidebarMenu">
          <h3 className="sidebarTitle">DashBoard</h3>
          <ul className="sidebarList">
            <li className="sidebarListItem active">
              <HomeIcon className="sidebarIcon" />
              <Link to="/admin" style={{ width: "100%" }}>
                <h5 className="sidebarIconText">Home</h5>
              </Link>
            </li>
            <li className="sidebarListItem">
              <TimelineIcon className="sidebarIcon" />
              <h5 className="sidebarIconText">Analytics</h5>
            </li>
            <li className="sidebarListItem">
              <AttachMoneyIcon className="sidebarIcon" />
              <h5 className="sidebarIconText">Sales</h5>
            </li>
          </ul>
        </div>
        <div className="sidebarMenu">
          <h3 className="sidebarTitle">Quick Menu</h3>
          <ul className="sidebarList">
            <li className="sidebarListItem">
              <PersonIcon className="sidebarIcon" />
              <Link to="/admin/users" style={{ width: "100%" }}>
                <h5 className="sidebarIconText">Users</h5>
              </Link>
            </li>
            <li className="sidebarListItem">
              <CardGiftcardIcon className="sidebarIcon" />
              <h5 className="sidebarIconText">Products</h5>
            </li>
            <li className="sidebarListItem">
              <AccountBalanceIcon className="sidebarIcon" />
              <h5 className="sidebarIconText">Transacions</h5>
            </li>
            <li className="sidebarListItem">
              <AssessmentIcon className="sidebarIcon" />
              <h5 className="sidebarIconText">Reports</h5>
            </li>
          </ul>
        </div>
        <div className="sidebarMenu">
          <h3 className="sidebarTitle">Notifications</h3>
          <ul className="sidebarList">
            <li className="sidebarListItem">
              <EmailIcon className="sidebarIcon" />
              <h5 className="sidebarIconText">Mail</h5>
            </li>
            <li className="sidebarListItem">
              <FeedbackIcon className="sidebarIcon" />
              <h5 className="sidebarIconText">Feedback</h5>
            </li>
            <li className="sidebarListItem">
              <MessageIcon className="sidebarIcon" />
              <h5 className="sidebarIconText">Message</h5>
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
};
