const initialState = [
  {
    id: "1",
    username: "Jon Snow",
    email: "jon@gmail.com",
    transaction: "120.00",
    status: "Active",
  },
  {
    id: "2",
    username: "Jon Snow",
    email: "jon@gmail.com",
    transaction: "120.00",
    status: "Active",
  },
  {
    id: "3",
    username: "Jon Snow",
    email: "jon@gmail.com",
    transaction: "120.00",
    status: "Active",
  },
];
const userReducer = (state = initialState, action) => {
  switch (action.type) {
    case "ADD_USER": {
      state = [...state, action.payload];
      return state;
    }
    case "DELETE_USER": {
      const filterState = state.filter((user) => {
        return user.id !== action.payload;
      });
      // console.log(filterState);
      state = filterState;
      return state;
    }
    case "EDIT_USER": {
      const updatedState = state.map((user) => {
        if (user.id === action.payload.id) {
          return action.payload;
        } else {
          return user;
        }
      });
      state = updatedState;
      return state;
    }
    default: {
      return state;
    }
  }
};
export default userReducer;
