import React, { useState } from "react";
import { Grid } from "@mui/material";
import { Sidebar } from "./components/Sidebar";
import { Topbar } from "./components/Topbar";
import { Home } from "./pages/Home";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import "./App.css";
import { UserList } from "./pages/UserList";
import { User } from "./pages/User";
import { NewUser } from "./pages/NewUser";
import MenuIcon from "@mui/icons-material/Menu";
function App() {
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const [grid_small, setGridSmall] = useState(9);
  const [grid_large, setGridLarge] = useState(10);
  const sidebarHandler = () => {
    setSidebarOpen(!sidebarOpen);
  };
  return (
    <Router>
      <Topbar style={{ marginBottom: "10px" }}></Topbar>
      <Grid container spacinng={2}>
        <Grid item xs={3} lg={2} className={sidebarOpen && "sidebarHide"}>
          <Sidebar></Sidebar>
        </Grid>
        <Grid
          item
          xs={sidebarOpen ? 12 : 9}
          lg={sidebarOpen ? 12 : 10}
          style={{ marginTop: "10px" }}
        >
          <MenuIcon onClick={sidebarHandler} style={{ position: "fixed" }} />
          <Switch>
            <Route path="/admin" exact>
              <Home />
            </Route>
            <Route path="/admin/users" exact>
              <UserList />
            </Route>
            <Route path="/admin/user/:id" exact>
              <User />
            </Route>
            <Route path="/admin/newuser">
              <NewUser />
            </Route>
          </Switch>
        </Grid>
      </Grid>
    </Router>
  );
}

export default App;
